import React, {useContext} from "react";
import {useNavigate} from "react-router-dom";
import Ctx from "../Ctx";

export default () => {
    const {user, setUser} = useContext(Ctx);
    const navigate = useNavigate();

    const logOut = (e) => {
        e.preventDefault();
        setUser("");
        localStorage.removeItem("user");
        navigate("/")
    }
    return <>
        <h1> </h1>
        <p>, {user && user.name}</p>
        <a href="" onClick={logOut}>  </a>
    </>
}